.headlineContentM {
  font-size: 2rem;
  font-weight: 500;
  color: #00d4ff;
  margin-bottom: 50px;
  line-height: 1.5;
}

.headlineContent1M {
  font-size: 2rem;
  font-weight: 500;
  color: black;
  margin-bottom: 50px;
  line-height: 1.5;
}

/* .rightM {
  display: none;
} */
@media screen and (max-width:700px) {
  .headlineContent {
    font-size: 2rem;
    font-weight: 500;
    color: #00d4ff;
    margin-bottom: 50px;
    line-height: 1.5;
  }

  .headlineContent1 {
    font-size: 2rem;
    font-weight: 500;
    color: black;
    margin-bottom: 50px;
    line-height: 1.5;
  }

  .dil {
    width: 45rem;
    height: 20rem;
  }

  .right::after {
    background-size: contain;
    top: 30%;
    width: 263px;
    height: 25%;
    right: 47%;
  }

  .right::before {
    background-size: contain;
    width: 412px;
    height: 40%;
    top: 3%;
    right: -46px;
  }
}

@media screen and (max-width: 700px) {
  .section {
    height: auto;
    margin-top: 2em;
  }

  .section-details {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column-reverse;
    max-width: 100%;
    width: 100%;
  }

  .left {
    flex: 1;
    padding-left: 20px;
    padding-top: 30px;
    margin-top: 50px;
    width: 90%;
  }
}